const initialState = {
    game: [],
    screenshots: []
}


const detailsReducer = (state = initialState, action) =>{
    switch(action.type){
        case 'GAME_DETAILS':
            return {...state, 
                game: action.payload.game,
                screenshots: action.payload.screenshots
            }
        default:
            return {...state}
    }
}

export default detailsReducer;